@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Home */
.home{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  color: white;
  background-image: url(/car-insurance/static/media/home-background.a9e3508b.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: top;
  height: 95vh;
}

.home-elements{
  margin-top: 5%;
  transition: 3s;
}
.home-elements:hover{
  color: #d4af37;
}
.home-elements h1{
  font-size: 3rem;
}
.home-elements p{
  width: 720px;
  
}
.home-elements span{
  font-weight: bold;
}

/* Insurance */
.insurance{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  color: #fff;
  background-color: #000000;
  transition: 3s;
}
.insurance:hover{
  color: #d4af37;
}
.insurance-head{
  font-size: 35px;
  margin-bottom: 5px;
}

.boxes{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-top: 30px;
  margin-bottom: 5%;
}

#icon{
  padding: 5px;
  border-radius: 50%;
  font-size: 60px;
  background-color: #000000;
  transition: 2s;
}
#icon:hover{
  background-color: #d4af37;
}

.box h4{
  font-size: 24px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.box p{
  width: 320px;
  color: #999999;
}
.box button{
  padding: 10px 30px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 25px;
  border: 2px solid #d4af37;
  color: #d4af37;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  transition: 1s;
}
.box button:hover{
  background-color: #d4af37;
  color: #fff;
}

/* Finding */
.finding{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  background-color: #000000;
  color: #fff;
}

.finding-text{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  text-align: left;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.left{
  transition: 3s;
}
.left:hover{
  color: #d4af37;
}
.left h1{
  width: 500px;
}
.left p{
  width: 500px;
}

.right{
  background-color: #d4af37;
  padding: 50px 20px 10px 20px;
  transition: 3s;
}
.right:hover{
  color: #000000;
}
.right h2{
  font-weight: bolder;
  font-size: 20px;
}
.right p{
  width: 500px;
  margin-bottom: 25px;
}

.images{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-top: 3%;
  margin-bottom: 50px;
}
.images img{
  height: 200px;
  width: 300px;
  cursor: pointer;
  -webkit-filter: blur(1.5px);
          filter: blur(1.5px);
}
.images img:hover{
  -webkit-filter: blur(0px);
          filter: blur(0px);
}
/* BestCompanies */


/* Contact */
.contact{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  background-color: #000000;
  color: #fff;
  height: 60vh;
}
.contact-text{
  transition: 3s;
}
.contact-text:hover{
  color: #d4af37;
}
.contact-text p{
  width: 450px;
}

.contact-form{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 7%;
}
.contact-form input{
  background-color: transparent;
  color: #fff;
  padding: 10px 12px;
  font-size: 17px;
  font-weight: bold;
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #fff;
}
.contact-form textarea{
  background-color: transparent;
  color: #fff;
  font-size: 17px;
  font-weight: bold;
  padding: 10px 10px;
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #fff;
}
.contact-form button{
  padding: 10px 30px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 25px;
  border: 2px solid #d4af37;
  color: #d4af37;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  transition: 1s;
  margin-top: 20px;
  width: 125px;
}
.contact-form button:hover{
  background-color: #d4af37;
  color: #fff;
}


/* Responsive Design */

/* Portrait */
@media only screen and (max-width: 900px) {
  /* Home */
  .home{
    height: 70vh;
  }
  .home-elements h1{
    font-size: 25px;
  }
  .home-elements p{
    width: 365px;
    font-size: 15px;
  }

  /* Insurance */
  .box{
    margin-bottom: 20px;
  }

  /* Finding */
  .left h1{
    font-size: 35px;
    text-align: center;
    width: 300px;
  }
  .left p{
    width: 300px;
    font-size: 14px;
  }

  .right h2{
    font-size: 35px;
    text-align: center;
  }
  .right p{
    width: 330px;
    text-align: center;
  }

  .images img{
    margin-top: 25px;
  }

  /* BestCompanies */

  /* Contact */
  .contact{
    height: 120vh;
  }
  .contact-text{
    text-align: center;
  }
  .contact-text p{
    width: 300px;
  }

  .contact-form textarea{
    width: 300px;
  }
}
.navbar{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
    background-color: #000000;
}
.navbar-head{
    color: #fff;
    font-size: 2rem;
    text-decoration: none;
    font-weight: bold;
    margin-left: 3rem;
    transition: 0.5s;
}
.navbar-head:hover{
    color: #d4af37;
}

.menu-bars{
    margin-right: 3rem;
    font-size: 2rem;
    background: none;
}

.nav-menu{
    background: rgba(0, 0, 0, 0.9);
    width: 200px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    position: fixed;
    top: 0;
    right: -100%;
    transition: 2s;
}

.nav-menu.active{
    right: 0;
    transition: 1s;
}

.nav-text{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 30px;
}

.nav-text a{
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    width: 95%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    transition: 0.5s;
}

.nav-text a:hover{
    text-decoration: underline;
    color: #d4af37;
}

.nav-items{
    width: 100%;
}

.navbar-toggle{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 80px;
}

#burger-menu{
    color: #fff;
    transition: 0.5s;
}
#burger-menu:hover{
    text-decoration: underline;
    color: #d4af37;
}

span{
    margin-left: 16px;
}
